button {
  font-family: 'Oswald', sans-serif;
  border: 5em;
  cursor: pointer;
  outline: none;
  font-size: 16px;
  -webkit-transform: translate(0);
  transform: translate(0);
  background-image: linear-gradient(45deg, #ffffff, #ffffff7e);
  padding: 0.7em 2em;
  border-radius: 65px;
  box-shadow: 1px 1px 10px rgba(255, 255, 255, 0.438);
  transition: box-shadow 0.25s;
  color: white !important;
}

button .text {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(45deg, #4568dc, #b06ab3);
}

button:after {
  content: "";
  border-radius: 18px;
  position: absolute;
  margin: 4px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background: #000000f1;
}

button:hover {
  background-image: linear-gradient(-45deg, #0bf04c, #0d4d01);
  box-shadow: 0 12px 24px rgba(194, 0, 0, 0.1);
}

button:hover .text {
  background-image: linear-gradient(-45deg, #4568dc, #000000);
}



.the-links a{
  position: absolute;
  margin: 5px;
}

.the-links a {
  color: rgb(148, 181, 161);
  text-decoration: none;
  position: relative;
}

.the-links a::after{
  content: "";
  background: rgb(237, 232, 232);
	mix-blend-mode: exclusion;
  width: calc(100% + 20px);
  height: 0;
  position: absolute;
  bottom: -4px;
  left: -10px;
  transition: all .3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  color: #000000;
}

.the-links a:hover::after{
  height: calc(100% + 8px);
  color: #000000;
}






